import { Component, OnInit } from '@angular/core';
import { ApiService } from '../services/api.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-player',
  templateUrl: './player.component.html',
  styleUrls: ['./player.component.css']
})
export class PlayerComponent implements OnInit {
  players: any[] = [];
  teams: any[] = [];
  newPlayer: any = {};
  selectedPlayer: any = null;
  editForm: FormGroup;
 
  

  constructor(private apiService: ApiService, private fb: FormBuilder) { }

  ngOnInit(): void {
    this.loadPlayers();
    this.loadTeams();
  }

  loadPlayers(): void {
    this.apiService.getPlayers().subscribe(players => {
      this.players = players;
      console.log(players);
    });

    

  }
  registerForm: FormGroup = this.fb.group({
    name: ['', Validators.required],
    teamId: ['', Validators.required],
  });
  
  loadTeams(): void {
    this.apiService.getTeams().subscribe(teams => {
      this.teams = teams;
    });
  }

  onSubmit(): void {
    if (this.selectedPlayer) {
      this.apiService.updatePlayer(this.selectedPlayer.id, this.newPlayer).subscribe(() => {
        this.loadPlayers();
        this.clearForm();
      });
    } else {
      this.apiService.createPlayer(this.newPlayer).subscribe(() => {
        this.loadPlayers();
        this.clearForm();
      });
    }
  }

  updatePlayer(player: any): void {
    if (!this.registerForm.get('name') || !this.registerForm.get('teamId')) {
      console.error('Controles del formulario no encontrados');
      return;
    }
  
    const updatedPlayer = {
      id: player.id,
      name: this.registerForm.get('name')!.value,
      teamId: this.registerForm.get('teamId')!.value
    };
  
    console.log('Jugador actualizado:', updatedPlayer);
    // Aquí se enviaría el jugador actualizado al backend.
  }
  
  
  

  deletePlayer(id: number): void {
    this.apiService.deletePlayer(id).subscribe(() => {
      this.loadPlayers();
    });
  }

  clearForm(): void {
    this.newPlayer = {};
    this.selectedPlayer = null;
  }
}
